.container {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.social {
    display: flex;
    align-items: center;
    gap: 10px;
}

.social img {
    height: 15px;
    width: 15px;
}

.icon {
    cursor: pointer;
    opacity: 0.7;
}